.button {
   min-width: 116px;
   padding: 5px 24px;
   color: white;
   font-family: Montserrat;
   font-size: 14px;
   font-weight: 600;
   line-height: 20px;
   display: flex;
   align-items: center;
   justify-content: center;
   border: none;
   border-radius: 3px;
   text-transform: capitalize;
   cursor: pointer;
}

.disabled {
   background-color: rgba(0, 77, 153, 0.5);
   color: rgb(101, 112, 122);
   cursor: auto;
}

.default {
   background-color: rgba(0, 102, 204, 1);
}
.secondary {
   border: 1px solid rgba(0, 102, 204, 1);
   background-color: white;
   color: rgba(0, 102, 204, 1);
}

.red {
   background-color: rgba(204, 20, 57, 1);
}

.default:hover {
   background-color: rgba(0, 128, 255, 1);
}

.red:hover {
   background-color: rgba(255, 26, 71, 1);
}

.secondary:hover {
   background-color: rgba(229, 240, 250, 1);
}

.default:active {
   background-color: rgba(0, 89, 178, 1);
}

.secondary:active {
   background-color: rgba(201, 229, 255, 1);
}

.red:active {
   background-color: rgba(178, 18, 50, 1);
}

.button:disabled {
   background-color: rgba(0, 77, 153, 0.5);
   color: rgb(101, 112, 122);
}
