.item {
   display: flex;
   justify-content: flex-start;
   border-bottom: 1px solid rgba(229, 229, 229, 1);
}

.item:last-of-type {
   border: none;
}

.users {
   width: 100%;
}

.buttonsContainer {
   display: flex;
   justify-content: flex-end;
   gap: 12px;
   margin-bottom: 18px;
}

.container {
   width: 50%;
}

.thead {
   display: flex;
   background-color: rgba(229, 229, 229, 1);
   font-family: Roboto;
   font-size: 14px;
   font-weight: 600;
   line-height: 16.41px;
}

.thead tr {
   width: 100%;
}

.nameCol {
   width: 50%;
   padding: 11px 0 11px 17px;
   display: inline-block;
}

.ageCol {
   width: 50%;
   padding: 11px 0 11px 0px;
   font-weight: 600;
   display: inline-block;
}
